/*
 * Leaflet raw map component used by ./map.jsx (use that if you want a map)
 */

 'use strict';
 var React = require('react/addons');
 var Reflux = require('reflux');
 var LegendActions = require('../../../actions/legendActions.js');


module.exports = React.createClass({displayName: "exports",

	componentDidMount: function () {
	    var self=this;
	    $(this.getDOMNode()).colorpicker({
	        'color':self.props.color,
	    })
	    .on('changeColor',function(evt){
	    	self.props.onChangeColor? self.props.onChangeColor(evt.color.toHex(),self.props.level):null;
	    	LegendActions.changeColorFundingByType(evt.color.toRGB(), self.props.level.split("Level")[1]);
	    });
	     
	},

	render:function(){
				return (React.createElement("span", null, 
    				React.createElement("input", {type: "text", style: {'display':'none'}, className: "form-control", value: this.props.color}), 
    				this.props.label, 
    				React.createElement("span", {className: "input-group-addon"}, React.createElement("i", null))
			))
	}
})
