'use strict';

var React = require('react/addons')
var Reflux = require('reflux');
var LayerActions = require('../../../actions/layersAction.js');
var Toggler = require('../../commons/toggler.jsx').Toggler;
var TogglerContent = require('../../commons/toggler.jsx').TogglerContent;
var If = require('../../commons/if.jsx')
var CustomRadio = require('../../commons/customRadioButton.jsx').Radio;
var CustomRadioGroup = require('../../commons/customRadioButton.jsx').RadioGroup;
var Layer = require('./_layer.jsx');

var _ = require('lodash');
var ColorPicker=require('./_colorPicker.jsx');

var Store = require('../../../stores/pointsLayerStore.js');

module.exports = React.createClass({displayName: "exports",

 mixins: [Reflux.connect(Store)], 
  
  _changevisibility: function(id, value) {
    LayerActions.changeLayerValue(id,'visible',value); //TODO:property mame should be in a globar variable 
  },
  
  _onChangeOpacity:function(id,value){
    LayerActions.changeLayerValue(id,'opacity',value); //TODO:property mame should be in a globar variable 
  },

  _showByDepartment:function(){
    LayerActions.changeLayerValue('points','level','departament'); //TODO:property mame should be in a globar variable 
  },

 _showByMunicipality:function(){
    LayerActions.changeLayerValue('points','level','municipality'); //TODO:property mame should be in a globar variable 
 },


 render: function() {
  var level=this.state.level;

  return (
  React.createElement("li", null, 
    React.createElement(Toggler, {ref: "toggler"}, 
      React.createElement(TogglerContent, {visibleWhen: "collapsed"}, 
        React.createElement("div", {toggler: true, className: "toggler-btn"}, React.createElement("i", {className: "fa fa-plus-square-o"}))
      ), 
      React.createElement(TogglerContent, {visibleWhen: "expanded"}, 
        React.createElement("div", {toggler: true, className: "toggler-btn"}, React.createElement("i", {className: "fa fa-minus-square-o"}))
      ), 
      React.createElement(TogglerContent, {visibleWhen: "always"}, 
        
        React.createElement(Layer, {id: "points", title: i18n.t("layers.totalProjects"), 
          opacity: this.state.opacity, 
          onChangeOpacity: this._onChangeOpacity, 
          onChangeVisibility: this._changevisibility, 
          visible: this.state.visible})
      
      ), 
      React.createElement(TogglerContent, {visibleWhen: "expanded"}, 
        React.createElement("ul", null, 
          React.createElement("li", null, 
            React.createElement("h3", null, React.createElement(Message, {message: "layers.level"})), 
            React.createElement(CustomRadioGroup, null, 
              React.createElement(CustomRadio, {className: "horizontal", name: "departament", checked: (level=='departament')? true : false, onClick: this._showByDepartment, label: "layers.byDepartment"}), 
              React.createElement(CustomRadio, {className: "horizontal", name: "municipality", checked: (level=='municipality')? true : false, onClick: this._showByMunicipality, label: "layers.byMunicipality"})
            )
          ), 
          
           React.createElement("li", null, 
            

              _.map(_.keys(this.state.breaks),function(key){
                  var br=this.state.breaks[key];
                return (
                      React.createElement("div", null, 
                          React.createElement(ColorPicker, {level: key, onChangeColor: this._changeColor, label: br.value +'%', color: br.style.color})
                      ))
             
              }.bind(this))

            
          )
          
        )
      )
    )
  ));
  
}
});
